import { createStore } from 'vuex'

export default createStore({
  state: {
    name: 'üê¢',
		food: ['üçî']
},
getters: {
    foodCount (state) {
      return state.food.length
    }
},
mutations: {
    setName (state, payload) {
      //state.name += 'üê¢'
      state.name = payload
    },
    setFood (state, payload) {
			//ÂÉèÊòØË≥ºÁâ©ËªäÔºåÊõ¥Êñ∞ÂÆåvuex stateÂÜçÊõ¥Êñ∞localstorage
			state.food = payload
    }
},
actions: {
    changeName (context) {
			//ÂÉèÊòØÁôªÂÖ•ÂæåÂèñÂæóÂ∏≥Êà∂Ë≥áË®ä
      context.commit('setName')
    },
    getFood (context, payload) {
			fetch('...')
			.then(res=>res.json())
			.then(res=>{
	      context.commit('setFood', res)
			})
    }
}
})
